	module.exports = {"data":{"site":{"siteMetadata":{"title":"The WordTree Foundation","subtitle":"The WordTree Foundation studies the relationships between books, with a special interest in LDS scripture such as the Book of Mormon.","copyright":"","menu":[{"label":"Home","path":"/"},{"label":"News","path":"/news/"},{"label":"The Late War","path":"/the-late-war"},{"label":"About WordTree","path":"/about/"}],"author":{"name":"WordTree Foundation","email":"info@wordtree.org","github":"wordtreefoundation"}}},"markdownRemark":{"id":"/Users/duane/wordtree/wordtreefoundation.github.io/src/pages/pages/2018-03-04---about/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/books-76a24618e9bcfe33c9152df45b028749-0b4a2.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 805px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 17.639751552795033%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"books\"\n        title=\"\"\n        src=\"/static/books-76a24618e9bcfe33c9152df45b028749-0b4a2.jpg\"\n        srcset=\"/static/books-76a24618e9bcfe33c9152df45b028749-3690a.jpg 240w,\n/static/books-76a24618e9bcfe33c9152df45b028749-b384f.jpg 480w,\n/static/books-76a24618e9bcfe33c9152df45b028749-0b4a2.jpg 805w\"\n        sizes=\"(max-width: 805px) 100vw, 805px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>We study the relationships between books, with a special focus on The Book of Mormon and other religious texts. We are a small volunteer organization with members across the United States and Canada who are curious about the origins of the Book of Mormon and want to contribute to its study.</p>\n<h2>Projects</h2>\n<h3><a href=\"https://www.bookofmormonorigins.com\">Book of Mormon Origins</a></h3>\n<p>An open-source, public domain resource that adds missing footnotes to the Book of Mormon.</p>\n<h3><a href=\"http://wordtree.org/the-late-war\">The Late War Study</a></h3>\n<p>A computer-aided study that reveals some remarkable similarities between the Book of Mormon and The Late War Between the United States and Great Britain (published in 1816).</p>\n<h3><a href=\"https://github.com/wordtreefoundation/bomdb\">BomDB</a></h3>\n<p>A resource for computer-aided studies of the Book of Mormon. Contains a command-line tool (Linux/MacOS/Windows) that can be used to find specific verses across multiple editions, or produce full copies (with our without chapter/verse annotations) of each edition.</p>\n<div align=\"center\">\n  <img src=\"/wordtree-f867c00df28f7c8e5c0492a2126c9d22.svg\" width=\"200\">\n</div>","frontmatter":{"title":"About The WordTree Foundation","date":null,"description":null}}},"pathContext":{"slug":"/about"}}

